import throttle from 'lodash/throttle'
import { useEffect, useState } from 'react'
import DesktopPage from './DesktopPage'
import MobilePage from './MobilePage'
import UnBrowser from './UnBrowser';

enum PageType {
  unbrowser,
  desktop,
  mobile,
}

const PAGES = {
  [PageType.unbrowser]: <UnBrowser />,
  [PageType.desktop]: <DesktopPage />,
  [PageType.mobile]: <MobilePage />,
};

function Pages () {
  const [pageType, setPageType] = useState<PageType | undefined>()

  useEffect(
    () => {
      const handleResize = throttle(
        () => {
          if(/(QQ|MicroMessenger)\//.test(navigator.userAgent)) {
            setPageType(PageType.unbrowser)
            return
          }

          if(
            !/iPhone|iPad|HarmonyOS|Android/.test(navigator.userAgent)
            &&
            document.documentElement.clientWidth > 960
          ) {
            setPageType(PageType.desktop)
            return
          }

          setPageType(PageType.mobile)
        },
        150
      )

      window.addEventListener('resize', handleResize)
      handleResize()

      return () => window.removeEventListener('resize', handleResize)
    },
    []
  )

  if (pageType === undefined) {
    return null
  }

  return PAGES[pageType];
}

export default Pages
