function GuidePage () {
  const ios = /iPhone|iPad|iPod/.test(navigator.userAgent)

  return (
    <SubPage
      className="GuidePage"
      title="安装教程"
    >
      {
        ios
        ? (<GuideIos />)
        : (<GuideAndroid />)
      }

      <div className="GuideBlock ContactUsBlock">
        <div className="ContactUsTitle">联系我们</div>
        <label>官方网站</label>
        <a
          target="_blank"
          href={HuarsConfig.SITE_URL}
          className="link-hl"
        >{HuarsConfig.SITE_TITLE}</a>
        <label>商务合作</label>
        <a
          target="_blank"
          href={HuarsConfig.BUSINESS_URL}
          className="link-hl"
        >{HuarsConfig.BUSINESS_TITLE}</a>
      </div>
      <footer className="GuideFooter">
        <div className="GuideFooterContent">
          <IconLogo />
          <div className="GuideFooterDesc">
            <img src={ImageSiteName} />
            {
              ios ? (
                <div>
                  <img src={ImageFooterIconIos} />
                  必须使用Safari下载苹果版APP
                </div>
              ) : (
                <div>
                  <img src={ImageFooterIconAndroid} />
                  安卓稳定版
                </div>
              )
            }
          </div>
          <a
            className="link-hl"
            href={ios ? HuarsConfig.DOWNLOAD_IOS : HuarsConfig.DOWNLOAD_ANDROID}
          >
            点击下载
          </a>
        </div>
      </footer>
    </SubPage>
  )
}

export default GuidePage
