import { useEffect } from 'react'
import throttle from 'lodash/throttle'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import IndexPage from './IndexPage'
import GuidePage from 'pages/MobilePage/GuidePage'
import HuaweiPage from './HuaweiPage'
import XiaomiPage from './XiaomiPage'
import OppoPage from './OppoPage'
import VivoPage from './VivoPage'
import MeizuPage from './MeizuPage'
import OneplusPage from './OneplusPage'
import RealmePage from './RealmePage'
import IqooPage from './IqooPage'
import OthersPage from './OthersPage'
import SumsungPage from './SumsungPage'
import OtherbrandPage from './OtherbrandPage'
import WarningPage from './WarningPage'
import ReleasePage from './ReleasePage'

function MobilePage () {
  useEffect(
    () => {
      const de = document.documentElement

      const handleResize = throttle(
        () => {
          if (
            !/iPhone|iPad|HarmonyOS|Android/.test(navigator.userAgent)
            &&
            de.clientWidth > 960
          ) {
            de.style.fontSize = ''
            return
          }
          de.style.fontSize = 100 * (de.clientWidth / 750) + 'px';
        },
        150
      )

      window.addEventListener('resize', handleResize)
      handleResize()

      return () => {
        de.style.fontSize = ''
        window.removeEventListener('resize', handleResize)
      }
    },
    []
  )

  return (
    <div className="MobilePage">
      <BrowserRouter>
        <Routes>
          <Route path="/guide">
            <Route path="" element={<GuidePage />} />
            <Route path="huawei" element={<HuaweiPage />} />
            <Route path="xiaomi" element={<XiaomiPage />} />
            <Route path="oppo" element={<OppoPage />} />
            <Route path="vivo" element={<VivoPage />} />
            <Route path="meizu" element={<MeizuPage />} />
            <Route path="1plus" element={<OneplusPage />} />
            <Route path="realme" element={<RealmePage />} />
            <Route path="iqoo" element={<IqooPage />} />
            <Route path="others" element={<OthersPage />} />
            <Route path="sumsung" element={<SumsungPage />} />
            <Route path="otherbrand" element={<OtherbrandPage />} />
            <Route path="tips" element={<WarningPage />} />
            <Route path="release" element={<ReleasePage />} />
          </Route>
          <Route path="*" element={<IndexPage />} />
        </Routes>
    </BrowserRouter>
    </div>
    
  )
}

export default MobilePage
