import SubPage from 'components/SubPage'
import { ReactNode } from 'react'
import mergeClass from 'utils/mergeClass'

interface GuideDetailPageaProps {
  className?: string
  title: string
  children: ReactNode | ReactNode[]
  fullImage?: boolean
}

function GuideDetailPagea (
  {
    className,
    fullImage,
    ...props
  }: GuideDetailPageaProps
) {
  return (
    <SubPage
      className={mergeClass(
        'GuideDetailPagea',
        className,
        fullImage ? 'FullImage' : undefined
      )}
      {...props}
      lightMode
    />
  )
}

export default GuideDetailPagea
